import chalk from 'chalk'
import widget_model from '../e2aWidget/models/widget-model.js'
import { writeFileSync } from 'fs'

const clone = {
    prefix: '',
    user_dev: '',
    set_config: false,
    config: undefined,

    setPrefix(prefix) {
        clone.prefix = prefix
    },

    missingField(field, attribute_name, location) {
        if (field == undefined) {
            console.log('üôÅ ' + chalk.red.bold(`informa√ß√µes n√£o encontradas...`));
            console.log('ü§î ' + chalk.blue(`voc√™ adicionou o atributo [${attribute_name}] no ${location} ?`));
            return false
        } 
        return true
    },

    setConfig(config) {
        clone.set_config = true
        if(!clone.missingField(config.user_dev,'user_dev','config.json')) {
            return
        }
        if(!clone.missingField(config.user_dev.id,'user_dev.id','config.json')) {
            return
        }
        if(!clone.missingField(config.user_dev.name,'user_dev.name','config.json')) {
            return
        }
        clone.config = config
        clone.user_dev = config.user_dev
    },

    splash() {
        console.log("„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è");
        console.log(`  üü•üî∑ ${clone.prefix} widget`);
        console.log("  {}üü™ CLONE (v 1.0)");
        console.log("„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è„Ä∞Ô∏è");

        console.log(chalk.yellow.bold(`üë®‚Äçüíª Ol√° ${clone.user_dev.name}!`));
    },

    async init(namespace,id) {
        if(!clone.set_config) {
            console.log('üôÅ ' + chalk.red.bold(`config n√£o inicializado...`));
            console.log('ü§î ' + chalk.blue(`voc√™ chamou setConfig() ?`));
            return
        }
        clone.splash()
        let wg_info = chalk.green.bold(`[üè∑Ô∏è NS:${namespace}] [ü™™ ID:${id}]`)
        console.log('üëÄ procurando widget ' + wg_info);

        let path = `widgets/${namespace}/${id}`
        let widget = await widget_model.get(path)
        if(widget == null) {
            console.log('üôÅ ' + chalk.red.bold(`n√£o encontrado...`));
        } else {
            console.log('üëç ' + chalk.green.bold(`achei`));
            console.log('üí´ ' + chalk.blue(`clonando em na pasta `)+clone.config.widgets_folder);
            //console.log('widget',widget);        
            let content = "<template>\n\t"+widget.template+"\n</template>"
            content = content + "\n<script>\nexport default "+widget.script+"\n</script>"
            let file_path = clone.config.widgets_folder+'/'+widget.id+'.vue'
            await writeFileSync(file_path,content)
            console.log('üí´ ' + chalk.blue(`arquivo `)+widget.id+'.vue' + chalk.green.bold(` salvo com sucesso!`));
        }

        
        //server.init(creator.config)
    }
}


export default clone