import chalk from 'chalk'
import server_widgets from "../server/server-widgets.js"
import server_gadgets from "../server/server-gadgets.js"

const creator = {
    prefix: '',
    user_dev: '',
    set_config: false,
    config: undefined,

    setPrefix(prefix) {
        creator.prefix = prefix
    },

    missingField(field, attribute_name, location) {
        if (field == undefined) {
            console.log('üôÅ ' + chalk.red.bold(`informa√ß√µes n√£o encontradas...`));
            console.log('ü§î ' + chalk.blue(`voc√™ adicionou o atributo [${attribute_name}] no ${location} ?`));
            return false
        } 
        return true
    },

    setConfig(config) {
        creator.set_config = true
        if(!creator.missingField(config.user_dev,'user_dev','config.json')) {
            return
        }
        if(!creator.missingField(config.user_dev.id,'user_dev.id','config.json')) {
            return
        }
        if(!creator.missingField(config.user_dev.name,'user_dev.name','config.json')) {
            return
        }
        creator.config = config
        creator.user_dev = config.user_dev
    },

    splash() {
        console.log("üß±üß±üß±üß±üß±üß±üß±üß±üß±üß±üß±");
        console.log(`  üü•üî∑ ${creator.prefix} widget`);
        console.log("  {}üü™ CREATOR (v 1.0)");
        console.log("üß±üß±üß±üß±üß±üß±üß±üß±üß±üß±üß±");

        console.log(chalk.yellow.bold(`üë®‚Äçüíª Ol√° ${creator.user_dev.name}!`));
    },

    init() {
        if(!creator.set_config) {
            console.log('üôÅ ' + chalk.red.bold(`config n√£o inicializado...`));
            console.log('ü§î ' + chalk.blue(`voc√™ chamou setConfig() ?`));
            return
        }
        creator.splash()
        server_widgets.init(creator.config)
        server_gadgets.init(creator.config)
    }
}

export default creator